body {
  /* min-height: 100vh; */
  /* width: 100vw; */
}
.Card {
  margin: 1rem;
  padding: 1rem;
  background: #efefef;
  
  width: 85vw;
  border-left: 0.7rem solid rgb(49, 131, 208);
  /* border-radius: 1rem; */
}
.Card-Title {
  font-weight: 900;
  font-size: 1.3rem;
}

.card-table {
  background-color: #fff;
  border-collapse: collapse;
  text-align: center;
  margin-top: 1rem;
}

.card-table th {
  padding: 1rem;
  border-bottom: 1px solid #999;
  white-space: nowrap;
}

.card-table td {
  padding: 1rem;
  width: 3rem;
  white-space: nowrap;
}

